@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("/fonts/opensans.woff2") format("woff2"), url("/fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("/fonts/opensansbold.woff2") format("woff2"), url("/fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
  background-color: white;
  font-family: "Open Sans", sans-serif;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  min-width: 280px;
}

.main-container {
  flex-grow: 1;
  position: relative;
}

.container {
  margin: 0 auto;
  max-width: 260px;
  width: 100%;
  position: relative;
}

.inner-page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  min-width: 280px;
}

.inner-container {
  flex-grow: 1;
  position: relative;
}

@media (min-width: 768px) {
  .container {
    max-width: 668px;
  }
}
@media (min-width: 1150px) {
  .container {
    max-width: 1050px;
  }
}
.title {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  margin: 0;
}

.inner-title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  padding: 73px 0;
}

.button {
  color: #444444;
  background-color: white;
  mix-blend-mode: normal;
  border: 2px solid #444444;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  padding: 16px 20px;
  text-decoration: none;
  cursor: pointer;
  text-transform: uppercase;
}
.button:hover {
  color: white;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}

@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.about-us {
  position: relative;
}

.about-us__wrapper {
  padding-bottom: 58px;
  padding-top: 36px;
  padding-left: 5px;
}

.about-us__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  position: relative;
}

.about-us__item {
  list-style-type: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: black;
