.feedback__wrapper {
  padding-bottom: 45px;
}

.feedback__title {
  margin-bottom: 15px;
  padding-left: 1px;
}

.feedback__description {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  padding-bottom: 33px;
  padding-left: 2px;
}

