}

.feedback__prev,
.feedback__next {
  border: 1px solid #ededed;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  padding: 12px 40px 11px 38px;
}

.feedback__prev-icon:hover,
.feedback__next-icon:hover {
  fill: rgba(0, 0, 0, 0.6);
}
.feedback__prev-icon:focus,
.feedback__next-icon:focus {
  fill: rgba(0, 0, 0, 0.3);
  outline: none;
}
.feedback__prev-icon:active,
.feedback__next-icon:active {
  fill: rgba(0, 0, 0, 0.3);
  outline: none;
}

.wave::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 6px;
  background-image: url("../img/zigzag-line-mobile.svg");
}

